* {
	box-sizing: border-box;
	-webkit-box-sizing: border-box;
	outline: none;
}

*:active,
*:focus {
	outline: none;
}

html,
body {
	font-size: 1.3rem; /* Fall back */
	line-height: 1.5em;
	font-family: 'Roboto', Helvetica, Arial, sans-serif;
	padding: 0;
	margin: 0;
	background: #f4f4f4;
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-khtml-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}

a,
a:visited,
a:active {
	color: #e53935;
	text-decoration: none;
}

a:hover {
	color: #a12828;
	cursor: pointer;
}

img {
	max-width: 100%;
	height: auto;
	margin: 1em;
}

.text-right {
	text-align: right;
}

.text-center {
	text-align: center;
}

.description {
	line-height: 1.5;
	color: #444444;
}

.disclaimer {
	font-size: 0.7rem;
	font-style: italic;
	color: #939393;
	letter-spacing: 0.025em;
	line-height: 135%;
	margin-top: 2em;
}
.no-margin {
	margin: 0;
}

.no-select {
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-khtml-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}

.medium-text {
	font-size: 0.95rem;
}

.intl {
	font-size: 0.8rem;
}

.logo {
	width: 5rem;
	margin: 0.5em;
}

/* Layout */
.row,
.block {
	display: flex;
}

.row {
	width: 100%;
	max-width: 100%;
	margin: 0 auto;
}

.col {
	display: inline-block;
	vertical-align: middle;
}

.c5 {
	width: 5%;
}

.c10 {
	width: 10%;
}

.c15 {
	width: 15%;
}

.c20 {
	width: 20%;
}

.c25 {
	width: 25%;
}

.c30 {
	width: 30%;
}

.c35 {
	width: 35%;
}

.c40 {
	width: 40%;
}

.c45 {
	width: 45%;
}

.c50 {
	width: 50%;
}

.c55 {
	width: 55%;
}

.c60 {
	width: 60%;
}

.c65 {
	width: 65%;
}

.c70 {
	width: 70%;
}

.c75 {
	width: 75%;
}

.c80 {
	width: 80%;
}

.c85 {
	width: 85%;
}

.c90 {
	width: 90%;
}

.c95 {
	width: 95%;
}

.c100 {
	width: 100%;
}

.padding {
	padding: 1rem;
}

.side-padding {
	padding: 0 1rem;
}

.top-bottom-padding {
	padding: 1rem 0;
}

.flex {
	display: flex;
	align-items: center;
	justify-content: center;
}

.align-left {
	text-align: left;
	justify-content: left;
}

footer {
	background: #f8f8f8;
	padding: 0.5rem;
	font-size: 0.7rem;
}

/* Animations */

@keyframes blink {
	0% {
		color: #d2e5ff;
	}
	50% {
		color: #1f325c;
	}
	100% {
		color: #d2e5ff;
	}
}

/* Board */

#board {
	background: #f7f5e6;
	padding: 0;
	margin: 0;
	/* padding-top: 0.5rem;
	padding-bottom: 0.5rem; */
}

.board-row {
	display: block;
	border-top: 1px solid #1f325c;
	border-bottom: 1px solid #1f325c;
}

.letter,
.ball {
	display: inline-block;
	text-align: center;
	color: #d2e5ff;
	width: 6.25%;
	line-height: 1.75em;
}

.letter {
	color: #f4f4f4;
	background: #1f325c;
	padding: 0.25em;
	font-weight: 700;
}

.called.ball:not(.active) {
	color: #1f325c;
}

.active.ball {
	animation: blink 1s infinite;
}
.separator {
	min-height: 0.5em;
	background: #52658f;
}

/* Buttons */

#buttons {
	padding: 0.5em 1em;
	background: #52658f;
}

button {
	margin: 0 0.25em;
	border: 0;
	background: #1f325c;
	border-radius: 0.2em;
	font-size: 1rem;
	padding: 0.5em 1em;
	color: #f4f4f4;
	text-transform: uppercase;
}
button:hover {
	background: #6c7fa9;
}

#buttons button {
	margin: 0.25em;
}

button:disabled {
	background: #aaaaaa;
	cursor: not-allowed !important;
	pointer-events: all !important;
}

button:active {
	outline: none;
}

button:hover {
	cursor: pointer;
}

/* Speed Selector */

#speed {
	margin: 0.75em;
}

#speed span {
	text-transform: uppercase;
	font-size: 0.8em;
	color: #f4f4f4;
	font-weight: bold;
}

input[type='range'] {
	appearance: none;
	width: 10rem;
	height: 0.5rem;
	outline: none;
	border-radius: 0.25rem;
	margin: 0 1rem;
	-moz-transform: rotate(180deg);
	-webkit-transform: rotate(180deg);
	-o-transform: rotate(180deg);
	-ms-transform: rotate(180deg);
	transform: rotate(180deg);
}

input[type='range']::-webkit-slider-thumb {
	appearance: none;
	width: 1.5rem;
	height: 1.5rem;
	background-color: #1f325c;
	border: 0.25rem solid #f4f4f4;
	border-radius: 1rem;
	cursor: pointer;
}

/* Select */

.Select {
	margin: 0.5em auto;
	font-size: 0.8rem;
	width: 100%;
}

#buttons .Select {
	width: 100%;
	text-align: left;
	font-size: 0.8rem;
}
.Select-control,
.Select-control * {
	height: auto !important;
}

.Select-input > input {
	padding: 0;
}

.Select-placeholder,
.Select--single > .Select-control .Select-value {
	bottom: 0;
	color: #aaa;
	left: 0;
	line-height: 2.25em;
	font-size: 0.8rem;
}

/* Ball Graphic */

.ballcol {
	text-align: center;
	color: #1f325c;
}

#ball {
	width: 100%;
	display: flex;
	border-radius: 100%;
	position: relative;
	align-items: center;
	justify-content: center;
	max-width: 150px !important;
	margin: 0 auto;
	box-shadow: inset 0.5rem 0.5rem 1.5rem 0.25rem rgba(255, 255, 255, 0.3),
		inset -0.25rem -0.25rem 1.5rem 0.5rem rgba(0, 0, 0, 0.4);
}
@-moz-document url-prefix() {
	#ball {
		height: 150px;
	}
}
#ball:after {
	content: '';
	display: block;
	padding-bottom: 100%;
}

.content {
	text-align: center;
	border-radius: 100%;
	background: #f4f4f4;
	position: relative;
	min-width: 1rem;
	padding: 25%;
	box-shadow: inset -0.5rem -0.5rem 2rem 0 rgba(0, 0, 0, 0.16), 0 0 0 0.25rem #f4f4f4;
	background: radial-gradient(circle at 20% 20%, #f4f4f4, #efefef);
}

.content span {
	justify-content: center;
	align-items: center;
	position: absolute;
	text-align: center;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	display: inline-flex;
	font-weight: bold;
	font-size: 1rem;
	line-height: 1;
}

.content img {
	padding: 0.25rem;
}

#currentBall {
	box-shadow: inset 0.5rem 0.5rem 1.5rem 0.25rem rgba(255, 255, 255, 0.3),
		inset -0.25rem -0.25rem 1.5rem 0.5rem rgba(0, 0, 0, 0.4);
}

#ballcount {
	position: absolute;
	bottom: -2rem;
	color: #1f325c;
	text-transform: uppercase;
	font-size: 1rem;
	font-weight: bold;
	text-align: center;
}

#ballcount:before {
	content: 'Call #';
	color: #6c7fa9;
	margin-right: 0.25em;
}
/*
		let colors = { 'B': 'indigo', 'I': 'ruby', 'N': 'kelly', 'G': 'alice', 'O': 'coral' }
		let colors = { 'B': '#3c6478', 'I': '#cd594a', 'N': '#b5c689', 'G': '#43abc9', 'O': '#f58b4c' }
*/
.indigolight {
	background: #3c6478;
}

.indigodark {
	background: #093145;
}

.indigo {
	background: radial-gradient(circle at 20% 20%, #3c6478, #093145);
}

.alicelight {
	background: #43abc9;
}

.alicedark {
	background: #107896;
}

.alice {
	background: radial-gradient(circle at 20% 20%, #43abc9, #107896);
}

.rubylight {
	background: #cd594a;
}

.rubydark {
	background: #9a2617;
}

.ruby {
	background: radial-gradient(circle at 20% 20%, #cd594a, #9a2617);
}

.corallight {
	background: #f58b4c;
}

.coraldark {
	background: #c2571a;
}

.coral {
	background: radial-gradient(circle at 20% 20%, #f58b4c, #c2571a);
}

.kellylight {
	background: #b5c689;
}

.kellydark {
	background: #829356;
}

.kelly {
	background: radial-gradient(circle at 20% 20%, #b5c689, #829356);
}

.red {
	background: radial-gradient(circle at 20% 20%, #ff0000, #910000);
}

.red .content {
	border: 0.15rem solid #ff0000;
}

.white {
	background: radial-gradient(circle at 20% 20%, #f4f4f4, #b6b6b6);
}

.white .content {
	border: 0.15rem solid #ff0000;
}

.green {
	background: radial-gradient(circle at 20% 20%, #008000, #004a00);
}

.green .content {
	border: 0.15rem solid #008000;
}

.yellow {
	background: radial-gradient(circle at 20% 20%, #ffe52a, #f7bd00);
}

.yellow .content {
	border: 0.15rem solid #f7bd00;
}

.hopper {
	background: #f4f4f4;
	width: 100%;
	height: 125px;
	border-bottom: 2px solid #1f325c;
	margin-bottom: 20px;
}

/* Pattern */

#bingopattern {
	white-space: nowrap;
	text-align: center;
	display: block;
	width: 100%;
	margin: 1rem auto;
}
#bingopattern .Select {
	width: 10rem;
	max-width: 100%;
}

.pattern-col {
	display: inline-block;
	vertical-align: top;
}

.pattern {
	min-width: 655px;
}

.pattern-prev,
.pattern-next {
	display: inline-flex;
	height: 100%;
	background: #52658f;
	color: #f4f4f4;
	padding: 0px 12px;
	cursor: pointer;
	flex-direction: column;
	justify-content: center;
}

.pattern-prev:hover,
.pattern-next:hover {
	background: #6c7fa9;
}

.pattern-prev--disabled,
.pattern-next--disabled {
	display: inline-flex;
	height: 100%;
	background: #52658f;
	color: #aaa;
	padding: 0px 12px;
	cursor: default;
	flex-direction: column;
	justify-content: center;
}

.pattern-prev--disabled:hover,
.pattern-next--disabled:hover {
	background: #52658f;
}

.pattern-letter,
.pattern-slot {
	width: 5em;
	height: 5em;
	padding: 0.35em;
	font-size: 0.75rem;
	line-height: 1.75em;
	/* background: #f3f3f3; */
	background-color: #f7f5e6;
	text-align: center;
	position: relative;
	cursor: pointer;
	border: 1px solid #1f325c;
}

.pattern-letter {
	display: table-cell;
	vertical-align: middle;
	background: #1f325c;
	color: #f4f4f4;
	border: 1px solid #1f325c;
}

.pattern-slot.selected:after {
	background: #52658f;
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	content: '';
	border-radius: 100%;
	margin: 20%;
}

/* Media Queries */
@media (max-width: 500px) {
	html,
	body {
		font-size: 0.9rem;
	}

	section .col,
	section .flex,
	footer .col,
	footer .flex {
		width: 100% !important;
		display: block;
	}

	#buttons {
		margin: 0 auto;
		text-align: center;
	}

	.board {
		text-align: center;
		font-size: 1.25rem;
	}

	.board-row {
		display: inline-block;
	}

	.letter {
		background: #1f325c;
		color: #f4f4f4;
	}

	.letter,
	.ball {
		display: block;
		width: 2em;
		height: auto;
		line-height: 1.5em;
	}

	footer * {
		text-align: center;
	}
	#ballcount {
		position: absolute;
		right: initial;
		bottom: -2.25rem;
		color: #1f325c;
		text-transform: uppercase;
		font-size: 1rem;
		font-weight: bold;
		text-align: center;
	}
	#ball {
		margin-bottom: 2rem;
	}
}

.react-tabs {
	-webkit-tap-highlight-color: transparent;
	margin: 0;
	padding: 0;
}

.react-tabs__tab-list {
	/* border-bottom: 6px solid #52658f; */
	margin: 0;
	padding: 0;
	background: #52658f;
	width: 100%;
}

.menu {
	list-style: none;
	background: #52658f;
	color: #f4f4f4;
	font-size: 0.5em;
	line-height: 1.75em;
	text-align: left;
	padding: 0.5em 0.5em;
	margin: 0em;
	max-height: 470px;
	overflow: scroll;
}

.menuitem {
	cursor: pointer;
	text-overflow: ellipsis;
	white-space: nowrap;
	overflow: hidden;
	color: #8f8f8f;
}

.menuitem:hover {
	background-color: #6c7fa9;
}

.menuitem.selected {
	background: #1f325c;
	color: #f4f4f4;
}

.react-tabs__tab {
	display: inline-block;
	/* border: 1px solid transparent;
	border-bottom: none; */
	border: none;
	/* bottom: -1px; */
	position: relative;
	list-style: none;
	cursor: pointer;
	font-size: 1rem;
	font-family: system-ui;
	text-transform: uppercase;
	padding: 0.5em 1em;
	color: #8f8f8f;
}

.react-tabs__tab:hover {
	background: #6c7fa9;
}

.react-tabs__tab--selected {
	background: #1f325c;
	/* border-color: #aaa; */
	color: #f4f4f4;
	/* border-radius: 5px 5px 0 0; */
}

.react-tabs__tab--disabled {
	color: #aaa;
	cursor: default;
}

.react-tabs__tab:focus {
	box-shadow: 0 0 5px hsl(208, 99%, 50%);
	border-color: hsl(208, 99%, 50%);
	outline: none;
}

.react-tabs__tab:focus:after {
	content: '';
	position: absolute;
	height: 5px;
	left: -4px;
	right: -4px;
	bottom: -5px;
	background: #f4f4f4;
}

.react-tabs__tab-panel {
	display: none;
}

.react-tabs__tab-panel--selected {
	display: block;
}

.noselect {
	-webkit-touch-callout: none; /* iOS Safari */
	-webkit-user-select: none; /* Safari */
	-khtml-user-select: none; /* Konqueror HTML */
	-moz-user-select: none; /* Old versions of Firefox */
	-ms-user-select: none; /* Internet Explorer/Edge */
	user-select: none; /* Non-prefixed version, currently supported by Chrome, Opera and Firefox */
}

